import {
	promise,
	contentPromise
} from './js/promise.js'
export default {
	props: {
		tabs: Array
	},
	data() {
		return {
			num: 0
		}
	},
	methods: {
		exchange(index, nav) {
			this.num = index
			this.$emit('load','more')
			console.log('tab:', nav)
			let obj = {
				nav,
				loading: true,
				count:0
			}
			this.$store.commit('loading', obj)
			this.$store.commit('none',false)
			contentPromise(nav,0)
				.then(res => {
					if(res.length === 0){
						this.$store.commit('none',true)
					}else{
						this.$store.commit('none',false)
					}
					this.$store.commit('exChangeData', res)
					let obj = {
						nav,
						loading: false,
						count:0
					}
					this.$store.commit('loading', obj)
				}).catch(err => {
					console.log(err)
				})
		}
	}
}
